import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import invite from '@/components/invite' //
import ore_pool from '@/components/ore_pool' //
import pool from '@/components/pool' //
import account from '@/components/account' //
import pledge from '@/components/pledge' //



Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'index',
      component: index,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    {
      path: '/index/:share',
      name: 'index',
      component: index,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    {
      path: '/invite',
      name: 'invite',
      component: invite,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    {
      path: '/ore_pool',
      name: 'ore_pool',
      component: ore_pool,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    {
      path: '/pool',
      name: 'pool',
      component: pool,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    {
      path: '/account',
      name: 'account',
      component: account,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    {
      path: '/pledge/:id',
      name: 'pledge',
      component: pledge,
      meta: {
        keepAlive: true, //
        isBack: false, //
      }
    },
    
    

  ]
})



